.cards {
  padding: 4rem;
  background: linear-gradient(0deg, rgb(36, 33, 33) 0%, rgb(32, 29, 29) 100%);
  color: aliceblue;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.cards * {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

h1 {
  text-align: center;
}

.cards-container {
  display: flex;
  flex-flow: column;
  align-items: center;
  /* max-width: 1120px; */
  width: 90%;
  /* margin: 0 auto; */
  /* height: 100%; */
}

.cards-wrapper {
  position: relative;
  margin: 50px 0 0 50px;
  width: 100%;
}

.cards-items {
  margin-bottom: 24px;
  display: flex;
}

.cards-item {
  display: flex;
  flex: 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards-item-link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgb(153, 50, 204, 0.2);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards-item-img-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

/* .fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
} */

.cards-item-img-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 20px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.cards-item-img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards-item-img:hover {
  transform: scale(1.1);
}

.cards-item-info {
  padding: 20px 30px 30px;
}
.cards-item-text {
  color: aliceblue;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  font-size: 20px;
}

.cards-item-info:hover .cards-item-text {
  transition: all 0.3s ease-out 0s;

  color: #af4fdf;
}
